body {
  background: #EDF2EB;
}
.container {
  width: 100%;
  height: 100%;
  align-content: center;
  align-items: center;
}
/* ==== Table ====*/
table {
  margin-top: 10%;
  margin-left: 10%;
  border-collapse: collapse;
  width: 80%;
}
/* Month row */
thead tr:first-child th:not(:first-child) {
  background-color: #00ACFF;
}
/* Weeks row */
thead tr:not(:first-child) {
  background-color: #00BCF5;
}

/* - */
th {
  font-weight: 500;
}
tbody tr:nth-child(even){
  background: #B9E9FF
}

td, th:not(:first-child) {
  border: 1px solid darkgray;
}
td:nth-of-type(1) {
  border: 0;
  background: #EDF2EB;
}
th {
  padding: 6px 0px;
}
th:nth-child(n+5) {
  width: 5%;
}
td{
  text-align: center;
}
/* End of Table  */


/*  ====== Buttons ====== */
button, input[type="button"] {
  border: none;
  color: white;
  padding: 8px 16px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  cursor: pointer;
}

/* Delete button */
button {
  background-color: #f44336;
}
button:hover {
  background-color: #FF5436;
}
/* "Add" button */
input[type="button"] {
  background-color: #4CA000;
}
input[type="button"]:hover {
  background-color: #4CAF50;
}

/* Form */

form {
  padding-top: 40px;
  width: 80%;
  margin-left: 10%;
}

label {
  font-size: 16px;
  font-weight: 500;
  margin-right: 15px;
}
input[type=text], input[type="date"] {
  padding: 6px 12px;
  margin: 8px 0;
  box-sizing: border-box;
  margin-right: 15px;
}

input[type="date"],input[type="text"] {
  width: 150px;
  background-color: #E1E3E6;
  border: none;
}
td.colored{
  background-color: red;
}